  /**
   * Verificar se tarefa √© segura
   */
  isTaskSafe(taskDescription) {
    const task = taskDescription.toLowerCase();

    // Verificar palavras-chave bloqueadas
    for (const keyword of this.blockedKeywords) {
      if (task.includes(keyword)) {
        return {
          safe: false,
          reason: `BLOQUEADO: "${keyword}" n√£o √© permitido para ningu√©m`,
          blocked: true,
          severity: "HIGH",
        };
      }
    }

    // Verificar opera√ß√µes bloqueadas
    for (const operation of this.blockedOperations) {
      if (task.includes(operation)) {
        return {
          safe: false,
          reason: `BLOQUEADO: ${operation} n√£o √© permitido`,
          blocked: true,
          severity: "HIGH",
        };
      }
    }

    // Verificar arquivos bloqueados
    for (const file of this.blockedFiles) {
      if (task.includes(file)) {
        return {
          safe: false,
          reason: `BLOQUEADO: Acesso ao arquivo "${file}" n√£o √© permitido`,
          blocked: true,
          severity: "HIGH",
        };
      }
    }

    return {
      safe: true,
      reason: "Tarefa permitida",
      blocked: false,
      severity: "LOW",
    };
  }

  /**
   * Filtrar conte√∫do gerado
   */
  filterContent(content) {
    let filtered = content;

    // Remover refer√™ncias a planos
    filtered = filtered.replace(/plano\s+(pro|premium|enterprise|b√°sico)/gi, "[BLOQUEADO]");
    filtered = filtered.replace(/upgrade\s+para/gi, "[BLOQUEADO]");
    filtered = filtered.replace(/billing|pagamento/gi, "[BLOQUEADO]");

    // Remover refer√™ncias a limites
    filtered = filtered.replace(/limite\s+de\s+requisi√ß√µes/gi, "[BLOQUEADO]");
    filtered = filtered.replace(/rate\s+limit/gi, "[BLOQUEADO]");
    filtered = filtered.replace(/quota/gi, "[BLOQUEADO]");

    // Remover dados sens√≠veis
    filtered = filtered.replace(/api[_-]?key|secret|token/gi, "[BLOQUEADO]");
    filtered = filtered.replace(/senha|password/gi, "[BLOQUEADO]");
    filtered = filtered.replace(/cart√£o|card|cpf|cnpj/gi, "[BLOQUEADO]");

    return filtered;
  }

