  /**
   * Validar requisi√ß√£o completa
   */
  validateRequest(githubUrl, task, mode) {
    // Validar URL
    if (!githubUrl || !githubUrl.includes("github.com")) {
      return {
        valid: false,
        error: "URL do GitHub inv√°lida",
      };
    }

    // Validar tarefa
    const safetyCheck = this.isTaskSafe(task);
    if (!safetyCheck.safe) {
      return {
        valid: false,
        error: safetyCheck.reason,
        blocked: true,
        severity: safetyCheck.severity,
      };
    }

    // Validar modo
    if (!["MANUAL", "AUTONOMOUS"].includes(mode.toUpperCase())) {
      return {
        valid: false,
        error: "Modo inv√°lido (use MANUAL ou AUTONOMOUS)",
      };
    }

    return {
      valid: true,
      message: "Requisi√ß√£o validada com sucesso",
    };
  }

